export default {
  /*  */
  "imageHost": "//fuss10.elemecdn.com",
  /*  */
  "proxyServer": "https://cors-proxy.leanapp.cn",
  // "proxyServer": "http://localhost:3000",
  "tabBar": {
    /**
     * tab 
     * @type {CSSColor}
     * @required
     */
    "color": "#8e8e93",
    /**
     * tab 
     * @type {CSSColor}
     * @required
     */
    "selectedColor": '#0089dc',
    /**
     * tab 
     * @type {CSSColor}
     * @required
     */
    "backgroundColor": '#fff',
    /**
     * tab 
     * @type {CSSStyle}
     * @required
     */
    "borderStyle": {
      "box-shadow": "0 -2px 4px rgba(0, 0, 0, .1)"
    },
    /**
     * (),  base , '@'  './src'  alias
     * @type {String}
     * @default "./src/assets/tabbar"
     * @optional
     */
    // "iconDir": "@/assets/tabbar",
    /**
     * tab 25 tab
     * @type {Array}
     * @required
     */
    list: [{
      /**
       * key
       * @type {String}
       * @required
       */
      key: 'index',
      /**
       * 
       * @type {String}
       * @required
       */
      path: '/',
      /**
       * tab 
       * @type {String}
       * @required
       */
      text: '',
      /**
       *  svg
       * 
       * @type {String}
       * @optional
       */
      iconPath: '#indexRegular',
      /**
       *  svg
       * @type {String}
       * @optional
       */
      activeIconPath: '#index',
    }, {
      key: 'discover',
      text: '',
      iconPath: '#discoverRegular',
      activeIconPath: '#discover',
      path: '/discover',
    }, {
      key: 'order',
      text: '',
      iconPath: '#orderRegular',
      activeIconPath: '#order',
      path: '/order',
    }, {
      key: 'profile',
      text: '',
      iconPath: '#profileRegular',
      activeIconPath: '#profile',
      path: '/profile',
    }],
  }
}